* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }

body{
    background: #000;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: #fff;
}

.container .number {
    font-size: 6rem;
}

.btns-container {
    width: 40rem;
    display: flex;
    justify-content: space-around;
    margin-top: 5rem;
}

.increment{
    padding: 10px 20px;
    border-radius: 100px;
    font-size: 2rem;
    background-color: #141517;
    color: #fff;
    cursor: pointer;
}

.decrement{
    padding: 10px 20px;
    border-radius: 100px;
    font-size: 2rem;
    background-color: #141517;
    color: #fff;
    cursor: pointer;
}